.sorting-list{position:relative;display:flex;justify-content:space-between;padding-bottom:16px}.sorting-list__sorting-button{display:block;padding:0;font-family:inherit;background-color:#fff;border:none;text-decoration:none;cursor:pointer}.sorting-list__sorting-button:disabled{display:none}.sorting-list__sorting-button--active,.sorting-list__sorting-button:active{color:#4952b6;font-weight:900}.sorting-list__sorting-button:focus,.sorting-list__sorting-button:hover{color:#d7417a;outline:none}@media (min-width:760px){.sorting-list{padding-bottom:1.6vw}.sorting-list__sorting-button{font-size:1.8vw;line-height:2vw}.sorting-list__sorting-button:disabled{display:block;color:#000;cursor:auto}}