@font-face{font-family:Merriweather;src:url(merriweather-regular.woff2) format("woff2"),url(merriweather-regular.woff) format("woff");font-display:swap;font-weight:400;font-style:normal}@font-face{font-family:Merriweather;src:url(merriweather-900.woff2) format("woff2"),url(merriweather-900.woff) format("woff");font-display:swap;font-weight:900;font-style:normal}*,:after,:before{box-sizing:inherit}html{box-sizing:border-box}body{margin:0;padding:0;font-family:Merriweather,serif;font-size:14px;line-height:16px;color:#000;background-color:#f5f7fb}main{width:320px;margin:0 auto}.link{color:#000;text-decoration:none}.same-list{margin:0;padding:0;list-style:none}.visual-hidden{position:absolute;overflow:hidden;width:1px;height:1px;margin:-1px;padding:0;border:0;white-space:nowrap;clip:rect(0 0 0 0)}@media (min-width:760px){main{width:70%;min-width:600px;max-width:1300px}}