select{padding:5px 10px;font-family:inherit;border:1px solid #84cd95;border-radius:10px;cursor:pointer}select:hover{border-color:#4952b6}select:focus{border-color:#84cd95;box-shadow:inset 1px 1px #84cd95,inset -1px -1px #84cd95;outline:none}@media (min-width:760px){select{padding:.5vw 1vw;font-size:1.4vw;line-height:1.6vw;border-radius:1vw;border-width:.1vw}}